import api from "../../../../../constants/api";
import Service from "../../../../../services/request";

export const getData = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.LIST_CATEGORY.method,
      path: api.LIST_CATEGORY.path,
      data: params,
    }).then((response) => {
      dispatch({
        type: "GET_DATA_CATEGORY",
        data: response?.data?.data,
        totalPages: response?.data?.total,
        params,
      });
    });
  };
};
export const getKind = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.LIST_KIND.method,
      path: api.LIST_KIND.path,
      data: params,
    }).then((response) => {
      dispatch({
        type: "GET_DATA_KIND",
        data: response?.data?.data,
        totalPages: response?.data?.total,
        params,
      });
    });
  };
};

export const getCategory = (id) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_CATEGORY.method,
      path: api.GET_CATEGORY.path,
      query: { id },
    })
      .then((response) => {
        dispatch({
          type: "GET_CATEGORY",
          selected: response?.data,
        });
      })
      .catch((err) => console.log(err));
  };
};
export const getKindId = (categoryId) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_KIND.method,
      path: api.GET_KIND.path,
      query: { id: categoryId },
    })
      .then((response) => {
        dispatch({
          type: "GET_KindId",
          selected: response?.data,
        });
      })
      .catch((err) => console.log(err));
  };
};
export const addCategory = (Category) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.CREATE_CATEGORY.method,
      path: api.CREATE_CATEGORY.path,
      data: Category,
    })
      .then((response) => {
        dispatch({
          type: "ADD_CATEGORY",
          response,
        });
      })
      .catch((err) =>
        dispatch({
          type: "ADD_CATEGORY",
          err,
        })
      );
  };
};
export const updateCategory = (Category) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.UPDATE_CATEGORY.method,
      path: api.UPDATE_CATEGORY.path,
      data: Category,
    })
      .then((response) => {
        dispatch({
          type: "UPDATE_CATEGORY",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "UPDATE_CATEGORY",
          err,
        })
      );
  };
};
