import { useEffect, useState } from "react";
import { FormattedMessage, injectIntl } from "react-intl";
import avatarBlank from "./../../../../../assets/images/avatars/bg-blank.png";
import "react-phone-number-input/style.css";

import { isObjEmpty, scrollToTop } from "@utils";

import classnames from "classnames";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useParams } from "react-router-dom";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Media,
  Row,
} from "reactstrap";
import validateOptions from "@constants/validate";
import { updateCategory } from "../../store/action";
import { convertToRaw, EditorState } from "draft-js";
import { Editor } from "react-draft-wysiwyg";
import { htmlToDraftUtil } from "@utils";
import Select from "react-select";
import {} from "react-scroll-up";
import { getProduct, getProductId, updateDocument } from "../store/action";
import draftToHtml from "draftjs-to-html";
import { convertFileToBase64, uploadImage } from "../../../../../helper/common";

const CourseDocumentTab = ({ selected, intl }) => {
  const store = useSelector((state) => state.course_document);
  const { register, errors, handleSubmit } = useForm();
  const history = useHistory();
  const [categoryData, setCategoryData] = useState(null);
  const lang = useSelector((state) => state.common.language);
  const dispatch = useDispatch();
  const [status, setStatus] = useState(null);
  const [show_download, setShow_download] = useState(null);
  const [disable, setDisable] = useState(false);
  const { product } = useSelector((state) => state.course_document);
  const [changcate, setChangcate] = useState();
  const { id } = useParams();
  const [changeSelect, setChangeSelect] = useState(changcate);
  const EditDocumentOptions = validateOptions.EditDocumentOptions;
  const [valueContent, setValueContent] = useState(EditorState.createEmpty());
  const [selectedFile, setSelectedFile] = useState();
  const [File, setFile] = useState();
  const [urlFile, setUrlFile] = useState("");
  const handleFileSelect = async (event) => {
    const file = event.target.files[0];
    const imageBase64 = await convertFileToBase64(file);
    setFile(file);
    setSelectedFile(imageBase64);
    setDisable(true);
  };
  useEffect(() => {
    (async () => {
      if (selectedFile) {
        const newData = selectedFile.replace(/,/gi, "").split("base64");
        if (newData[1]) {
          const data = {
            imageData: newData[1],
            imageFormat: "pdf",
          };
          await uploadImage(data, File).then((res) => {
            setUrlFile(res?.data);
            setDisable(false);
            const fileurl = document.getElementById("fileurl");
            fileurl.value = res?.data;
          });
        }
      }
    })();
  }, [selectedFile, File]);
  const [valueShortContent, setValueShortContent] = useState(
    EditorState.createEmpty()
  );
  const changeCate = (e) => {
    setChangeSelect(e);
  };
  const uploadCallback = async (file) => {
    var linkImg = avatarBlank;
    const imageBase64 = await convertFileToBase64(file);
    if (imageBase64) {
      const newData = imageBase64.replace(/,/gi, "").split("base64");
      if (newData[1]) {
        const data = {
          imageData: newData[1],
          imageFormat: file?.type?.split("/")[1] || "png",
        };
        await uploadImage(data, file).then((res) => {
          linkImg = res?.data;
        });
      }
    }
    return new Promise((resolve, reject) => {
      resolve({ data: { link: linkImg } });
    });
  };
  useEffect(() => {
    if (store?.productDetail?.product_names != undefined) {
      let options = {
        value: store?.productDetail?.id,
        label: store?.productDetail?.product_names[0]?.name,
      };
      setChangcate(options);
    }
  }, [store]);
  useEffect(() => {
    if (changcate) {
      setChangeSelect(changcate);
    }
  }, [changcate]);
  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);
  useEffect(() => {
    dispatch(
      getProductId({
        id: selected?.productId,
        lang: lang,
      })
    );
  }, [dispatch, selected?.productId]);

  useEffect(() => {
    dispatch(
      getProduct({
        filter: {
          product_type: 0,
          lang,
        },
        skip: 0,
        limit: 20,
        order: [
          {
            key: "id",
            value: "desc",
          },
        ],
      })
    );
  }, [dispatch]);

  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null &&
        categoryData !== null &&
        selected.id !== categoryData.id)
    ) {
      setCategoryData(selected);
    }
  }, [selected, categoryData]);

  useEffect(() => {
    if (store.status === 200) {
      scrollToTop();
      history.push("/apps/course/document/list");
    }
  }, [store.status, history]);

  const onSubmit = async (values) => {
    const content = draftToHtml(convertToRaw(valueContent.getCurrentContent()));
    const short_content = draftToHtml(
      convertToRaw(valueShortContent.getCurrentContent())
    );
    if (isObjEmpty(errors)) {
      setDisable(true);
      dispatch(
        updateDocument({
          id: parseInt(id),
          data: {
            ...values,
            sort_order: parseInt(values.sort_order),
            status: parseInt(values.status),
            show_download: parseInt(values.show_download),
            fileurl: values?.fileurl ? values?.fileurl : "",
            lang,
            content,
            short_content,
            productId: parseInt(changeSelect?.value),
          },
        })
      );
    }
  };

  useEffect(() => {
    if (categoryData) {
      setValueContent(htmlToDraftUtil(categoryData?.content || " "));
      setValueShortContent(htmlToDraftUtil(categoryData?.short_content || " "));
      setStatus(categoryData?.status);
      setShow_download(categoryData?.show_download);
    }
  }, [categoryData]);

  return (
    <Row>
      <Col sm="12">
        <Media className="mb-2">
          <Media className="mt-50" body>
            <h4>{selected.name} </h4>
          </Media>
        </Media>
      </Col>
      <Col sm="12">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <FormGroup>
            <Label for="sort_order">
              <FormattedMessage id="sort_order" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="sort_order"
              id="sort_order"
              innerRef={register(EditDocumentOptions.sort_order)}
              onBlur={() => {
                let sort_order = document.getElementById("sort_order");
                if (sort_order && sort_order.value) {
                  sort_order.value = sort_order.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["sort_order"] })}
              defaultValue={categoryData && categoryData.sort_order}
            />
            <small className="text-danger">
              {errors?.sort_order && errors.sort_order.message}
            </small>
            {errors?.sort_order?.type === "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid category sort_order" />
              </small>
            )}
          </FormGroup>
          <FormGroup>
            <Label for="name">
              <FormattedMessage id="Document" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="name"
              id="name"
              innerRef={register(EditDocumentOptions.name)}
              onBlur={() => {
                let name = document.getElementById("name");
                if (name && name.value) {
                  name.value = name.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["name"] })}
              defaultValue={categoryData && categoryData.name}
            />
            <small className="text-danger">
              {errors?.name && errors.name.message}
            </small>
            {errors?.name?.type === "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid category name" />
              </small>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="author">
              <FormattedMessage id="author" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="author"
              id="author"
              innerRef={register(EditDocumentOptions.author)}
              onBlur={() => {
                let name = document.getElementById("author");
                if (name && name.value) {
                  name.value = name.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["author"] })}
              defaultValue={categoryData && categoryData.author}
            />
            <small className="text-danger">
              {errors?.author && errors.author.message}
            </small>
            {errors?.author?.type === "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid category name" />
              </small>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="fileurl">
              <FormattedMessage id="fileurl" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="fileurl"
              id="fileurl"
              innerRef={register(EditDocumentOptions.fileurl)}
              onBlur={() => {
                let fileurl = document.getElementById("fileurl");
                if (fileurl && fileurl.value) {
                  fileurl.value = fileurl.value.trim();
                }
              }}
              className={classnames({ "is-invalid": errors["fileurl"] })}
              defaultValue={categoryData && categoryData.fileurl}
            />
            <small className="text-danger">
              {errors?.fileurl && errors.fileurl.message}
            </small>
            {errors?.fileurl?.type === "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid category name" />
              </small>
            )}
          </FormGroup>
          <FormGroup>
            <Input
              type="file"
              name="fileurl"
              id="fileurl"
              onChange={handleFileSelect}
              className={classnames({ "is-invalid": errors["fileurl"] })}
            />
            <small className="text-danger">
              {errors?.fileurl && errors.fileurl.message}
            </small>
            {errors?.fileurl?.type === "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid category name" />
              </small>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="content">
              <FormattedMessage id="Content" />{" "}
              <span className="text-danger"></span>
            </Label>

            <Editor
              stripPastedStyles={true}
              toolbar={{
                // options: ["inline", "textAlign"],
                // inline: {
                //   inDropdown: false,
                //   options: ["bold", "italic", "underline"],
                // },
                image: { uploadCallback: uploadCallback },
              }}
              defaultEditorState={valueContent}
              editorState={valueContent}
              onEditorStateChange={(data) => setValueContent(data)}
              name="content"
              // innerRef={register(Document_course.content)}
            />
            <small className="text-danger">
              {errors?.content && errors.content.message}
            </small>
          </FormGroup>
          <FormGroup>
            <Label for="short_content">
              <FormattedMessage id="short_content" />{" "}
              <span className="text-danger"></span>
            </Label>

            <Editor
              stripPastedStyles={true}
              toolbar={{
                options: ["inline", "textAlign"],
                inline: {
                  inDropdown: false,
                  options: ["bold", "italic", "underline"],
                },
              }}
              defaultEditorState={valueShortContent}
              editorState={valueShortContent}
              onEditorStateChange={(data) => setValueShortContent(data)}
              name="short_content"
              // innerRef={register(Document_course.short_content)}
            />
            <small className="text-danger">
              {errors?.short_content && errors.short_content.message}
            </small>
          </FormGroup>
          <FormGroup>
            <Label>
              <FormattedMessage id="Course" />
              <span className="text-danger"> * </span>
            </Label>
            <Select
              isClearable={false}
              onChange={(e) => changeCate(e)}
              innerRef={register({ required: true })}
              name="productId"
              value={changeSelect}
              placeholder={<FormattedMessage id="Select Course" />}
              options={product?.map((item, index) => {
                return {
                  value: item?.id,
                  label: item?.product_names[0]?.name,
                };
              })}
              className="react-select"
              classNamePrefix="select"
            />
            <small className="text-danger">
              {errors?.productId && errors.productId.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label for="show_download">
              <FormattedMessage id="show_download" />
            </Label>

            <Input
              type="select"
              name="show_download"
              value={show_download}
              onChange={(e) => setShow_download(e.target.value)}
              id="show_download"
              innerRef={register({ required: true })}
            >
              <option value="1">
                {intl.formatMessage({ id: "Allow download" })}
              </option>
              <option value="0">
                {intl.formatMessage({ id: "Do not allow download" })}
              </option>
            </Input>
          </FormGroup>

          <FormGroup>
            <Label for="status">
              <FormattedMessage id="Status" />
            </Label>

            <Input
              type="select"
              name="status"
              value={status}
              onChange={(e) => setStatus(e.target.value)}
              id="status"
              innerRef={register({ required: true })}
            >
              <option value="1">{intl.formatMessage({ id: "Active" })}</option>
              {/* <option value="0">{intl.formatMessage({ id: "Deactive" })}</option> */}

              <option value="0">
                {intl.formatMessage({ id: "Deactive" })}
              </option>
            </Input>
          </FormGroup>

          <div style={{ textAlign: "end", marginTop: "145px" }}>
            <Button
              type="submit"
              className="mr-1"
              color="primary"
              disabled={disable}
            >
              <FormattedMessage id="update" />
            </Button>
            <Button
              type="reset"
              color="secondary"
              outline
              onClick={() => history.goBack()}
            >
              <FormattedMessage id="Cancel" />
            </Button>
          </div>
        </Form>
      </Col>
    </Row>
  );
};
export default injectIntl(CourseDocumentTab);
