export const remove = (id) => {
  return async (dispatch) => {
    await Service.send({
      method: api.DELETE_COURSE_DOCUMENT.method,
      path: api.DELETE_COURSE_DOCUMENT.path,
      query: id,
    })
      .then((response) => {
        dispatch({
          type: "DELETE_COURSE_DOCUMENT",
          response,
        });
      })
      .catch((err) => console.log(err));
  };
};

