export const getDocument = (id) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_DOCUMENT_COURSE.method,
      path: api.GET_DOCUMENT_COURSE.path,
      query: { id },
    })
      .then((response) => {
        dispatch({
          type: "GET_DOCUMENT_COURSE",
          selected: response?.data,
        });
      })
      .catch((err) => console.log(err));
  };
};
