export const addDocument = (Document) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.CREATE_DOCUMENT_COURSE.method,
      path: api.CREATE_DOCUMENT_COURSE.path,
      data: Document,
    })
      .then((response) => {
        dispatch({
          type: "ADD_DOCUMENT",
          response,
        });
      })
      .catch((err) =>
        dispatch({
          type: "ADD_DOCUMENT",
          err,
        })
      );
  };
};
export const updateDocument = (Category) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.UPDATE_DOCUMENT_COURSE.method,
      path: api.UPDATE_DOCUMENT_COURSE.path,
      data: Category,
    })
      .then((response) => {
        dispatch({
          type: "UPDATE_DOCUMENT_COURSE",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "UPDATE_DOCUMENT_COURSE",
          err,
        })
      );
  };
};
